          <div>11.11. 타인의 개인정보, 등록 정보, 이용 이력 정보 등을 불법으로 수집, 공개 또는 제공하는 행위.</div>
          <div>11.12. 본 서비스의 서버와 네트워크 시스템에 지장을 주는 행위, BOT, 치팅 툴, 기타 기술적인 수단을 이용하여 서비스를 불법으로 조작하는 행위, 본 서비스의 장애를 의도적으로
            이용하는 행위, 동일한 질문을 필요 이상으로 반복하는 등 서비스 운영주체에 대해 부당한 문의 또는 요구를 하는 행위, 기타 서비스 운영주체의 본 서비스 운영 또는 다른 고객의 본 서비스 이용을 방해하거나 이에 지장을
            주는 행위.
          </div>
          <div>11.13. 상기11.1.부터 11.12까지의 어느 하나에 해당되는 행위를 원조 또는 조장하는 행위.</div>
          <div>11.14. 기타 부적당하다고 서비스 운영주체가 판단한 행위.</div>
          <div><br></div>

          <div><b>12. 고객의 책임</b></div>
          <div><br></div>
          <div>12.1. 고객은 고객 자신의 책임 하에 본 서비스를 이용해야 하며, 본 서비스에서 이루어진 모든 행위 및 그 결과에 대해서 일체의 책임을 져야 합니다.</div>
          <div>12.2. 서비스 운영주체는 고객이 본 약관에 위반되는 형태로 본 서비스를 이용하고 있다고 인정한 경우, 서비스 운영주체에서 필요하고 적절하다고 판단한 조치를 취하겠습니다. 단, 서비스 운영주체는 이러한 위반 행위를 방지
            또는 시정할 의무를 갖지 않습니다.
          </div>
          <div>12.3. 본 서비스 이용에 기인하여(서비스 운영주체가 이러한 이용으로 인해 클레임을 제삼자로부터 받은 경우를 포함함)서비스 운영주체가 직접적 혹은 간접적으로 어떤 손해(변호사 비용 부담을 포함함)를 입었을
            경우, 고객은 서비스 운영주체의 요구에 따라 즉시 이를 보상해야 합니다.
          </div>
          <div><br></div>

          <div><b>13. 서비스 운영주체 면책</b></div>
          <div><br></div>
          <div>13.1. 서비스 운영주체는 본 서비스(본 컨텐츠를 포함함)에 사실상 또는 법률상의 하자(안전성, 신뢰성, 정확성, 완전성, 유효성, 특정 목적과 부합 여부, 보안 등에 관련된 결함, 오류와 버그,
            권리 침해 등을 포함함)가 없음을 명시적으로도 암묵적으로도 보증하지 않습니다. 서비스 운영주체는 고객에 대해 이러한 하자를 제거하고 본 서비스를 제공할 의무를 갖지 않습니다.
          </div>
          <div>13.2. 서비스 운영주체는 본 서비스로 인하여 고객에게 발생한 모든 손해에 대해 일체의 책임을 지지 않습니다. 단, 본 서비스에 관한 서비스 운영주체와 고객 간의 계약(본 약관을 포함함)이 소비자계약법에서
            규정하는 소비자 계약에 해당될 경우, 이 면책 규정은 적용되지 않습니다.
          </div>
          <div>13.3. 상기 14.2.의 단서 조항에서 규정하는 경우라도 서비스 운영주체의 과실(중과실을 제외함)에 의한 채무 불이행 또는 불법 행위로 인해 고객에게 발생한 손해 중 특별한 사정으로 발생한
            손해(서비스 운영주체 또는 고객이 손해 발생을 예견했거나 또는, 예견할 수 있었던 경우를 포함함)에 대해 서비스 운영주체는 일체의 책임을 지지 않습니다. 또한, 서비스 운영주체의 과실(중과실을 제외함)에 의한 채무 불이행 또는
            불법 행위로 인해 고객에게 발생한 손해를 배상할 때는 고객으로부터 당해 손해가 발생한 달에 수령한 이용료 금액을 상한 금액으로 합니다.
          </div>
          <div><br></div>
          <div><b>14. 연락 방법</b></div>
          <div><br></div>
          <div>14.1. 본 서비스와 관련해 서비스 운영주체가 고객에게 연락을 할 때는 서비스 운영주체가 운영하는 웹사이트 내의 적당한 장소에 게시하거나 기타 서비스 운영주체가 적당하다고 판단하는 방법으로 합니다.</div>
          <div>14.2. 본 서비스와 관련해 고객이 서비스 운영주체에 연락을 할 때는 서비스 운영주체가 운영하는 웹사이트 내의 적당한 장소에 설치된 고객문의 페이지에서 송신하거나 서비스 운영주체가 지정하는 방법으로 합니다.</div>
          <div><br></div>

          <div align="right" style="text-align: right;">끝.</div>

        </div>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: "TermsOfService",
    methods: {
        goHome() {
          window.location.href = '/'
        }
    }
  }
</script>

<style scoped>
  header.top-header {
    border-bottom: 1px solid #00439f;
    margin-bottom: 15px;
    height: 38px;
    background-color : #00439f;
  }
  img.logo {
    height: 20px;
    vertical-align: middle;
  }

  section.privacy-policy {
    margin-bottom: 48px;
  }

  div.center-block {
    width: 1020px;
    margin: auto;
  }

  h1 {
    margin-bottom: 32px;
  }
  .flex-con {
    display:flex;
    flex-direction: row;
    align-items: center;
  }
  .flex-item {
    display:inline-block;
    padding: 4px;
    margin-left: 4px;
    margin-right: 4px;
  }

  .privacy-policy {
    line-height: 1.2;
  }
  .privacy-policy b {
    font-weight: bold;
  }

  a {
    font-weight: bold;
    font-size: 1.2em;
    color: #004e8c;
  }
</style>



